.c{
    height: 100vh;
    position: relative;
}
.c-bg{
    width: 20px;
    height: 100%;
    background-color: rgb(75, 70, 155);
    position: absolute;
}
.c-wrapper{
    padding: 50px;
    display: flex;
}
.c-left{
    flex: 1;
}
.c-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.c-title{
    font-size: 60px;
    width: 80%;
    text-align: left;
}
.c-info-item{
    display: flex;
    align-items: center;
    margin:  50px 0px;
    width: 70%;
    font-weight: 300;
}

.c-icon{
    width: 30px;
    height: 30px;
    margin-right: 20px;
}
.c-desc{
    font-weight: 200;
    text-align: left;
}

form{
    margin-top: 20px;
}

input{
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
button{
    border: none;
    padding: 15px;
    background-color: rgb(35, 198, 219);
    color: white;
    font-weight: 500;
    cursor: pointer;
    border-radius: 10%;
}

@media screen and (max-width: 480px) {
    .c{
        height: 100%;
    }
    .c-wrapper{
        flex-direction: column ;
        padding: 0px 50px;
    }
    .c-title{
        font-size: 30px;
    } 
    .c-info-item{
        margin: 20px 0px;
        width: 100%;
    } 
    .c-desc{
        display: none;
    } 
    form{
        margin-top: 0;
        display: flex;
        flex-wrap: wrap;
    } 
    input{
        width: 35%;
        height: 25px;
        margin: 10px;
        margin-left: 0;
    }
    textarea{
        height: 25px;
    }
    button{
        margin: 10px;
    }
}